
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
}
a {
  color: #2c3e50;
  text-decoration: none;
}
a:active {
  color: #2c3e50;
}
a:visited {
  color: #2c3e50;
}
#sidebar {
  position: fixed;
  left: 0;
  top: 0;
  height: 100%;
  width: 300px;
  margin: 0;
}
ul {
  padding-left: 20px;
  height: 100%;
  overflow-x: hidden;
  background-color: #eee;
}
.content {
  margin-left: 320px;
  text-align: center;
}



